<template>
  <div id="add-blog">
    <h2>添加博客</h2>
    <form  v-if="!submmited">
      <label>博客标题</label>
      <input type="text" v-model="blog.title"/>
      <label>博客内容</label>
      <textarea v-model="blog.content"></textarea>
      <div id="checkboxes">
        <label>vue.js</label>
        <input type="checkbox" value="vue.js" v-model="blog.cates"/>
        <label>react.js</label>
        <input type="checkbox" value="react.js" v-model="blog.cates"/>
      </div>
      <label>作者：</label>
      <select v-model="blog.author">
        <option v-for="author in authors" :key="author">
          {{author}}
        </option>
      </select>
      <button v-on:click.prevent="post">添加博客</button>
    </form>

    <div v-if="submmited">
      <h3>您的博客发布成功!</h3>
    </div>


    <div id="preview">
      <h3>博客总览</h3>

      <p>
        {{blog.content}}
      </p>
      <h3>博客分类</h3>
      <ul>
        <li v-for="cate in blog.cates ">
          {{cate}}
        </li>
      </ul>
      <h3>作者</h3>
      <p>
        {{blog.author}}
      </p>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
    export default {
      //http://jsonplaceholder.typicode.com/
      //http://jsonplaceholder.typicode.com/posts
        name: 'add-blog',
      data () {
          return {
            blog: {
              title: '',
              content: '',
              cates: [],
              author: ''
            },
            authors: ['jack', 'legend','honey'],
            submmited:false
          }
      },
      methods: {
          post: function () {
            // this.$http.post("https://wd8582252366authik.wilddogio.com/posts.json", this.blog
            //   {
            //   // title: this.blog.title,
            //   // body: this.blog.cotent,
            //   // userId: 1
            //
            // }
            axios.post("/posts.json",this.blog)
              .then((data) => {
                // console.log(data);
                this.submmited = true;
              });

              // .then(function (data) {
              //   console.log(data);
              //   this.submmited = true;
              // });
          }
      }
    }
</script>

<style scoped>
  #add-blog *{
    box-sizing: border-box;
  }

  #add-blog{
    margin: 20px auto;
    max-width: 600px;
    padding: 20px;
  }

  label{
    display: block;
    margin: 20px 0 10px;
  }

  input[type="text"],textarea,select{
    display: block;
    width: 100%;
    padding: 8px;
  }

  textarea{
    height: 200px;
  }

  #checkboxes label{
    display: inline-block;
    margin-top: 0;
  }

  #checkboxes input{
    display: inline-block;
    margin-right: 10px;
  }

  button{
    display: block;
    margin: 20px 0;
    background: crimson;
    color: #fff;
    border: 0;
    padding: 14px;
    border-radius: 4px;
    font-size: 18px;
    cursor: pointer;
  }

  #preview{
    padding: 10px 20px;
    border: 1px dotted #ccc;
    margin: 30px 0;
  }

  h3{
    margin-top: 10px;
  }

</style>



