<template>
    <div id="single-blog">

      <h2>{{blog.title}}</h2>
      <!--<article>{{blog.body}}</article>-->
      <article>{{blog.content}}</article>
      <p>作者: {{blog.author}}</p>
      <p>分类:</p>
      <ul>
        <li v-for="category in blog.cates" :key="category">
          {{category}}
        </li>
      </ul>
    </div>
</template>

<script>
  import axios from 'axios'
    export default {
        name: "single-blog",
      data(){
          return{
            id: this.$route.params.id,
            blog: {}
          }
      },
      created(){
          //this.$http.get('http://jsonplaceholder.typicode.com/posts/' + this.id+ '.json')
        axios.get('/posts/' + this.id+ '.json')
            // .then(function (data) {
            //   this.blog = data.body;
            //   return data.json();
            //   //console.log(data);
            // })
            // .then(function(data){
            //   this.blog=data;
            // })
          .then(
            (data)=>{
              console.log(data);
              this.blog=data.data;
            }
          )
      }
    }
</script>

<style scoped>

</style>



